import Session from './Session'
import Chat from './Chat'
import Search from './search'
import Info from './Info'

export default {
  components: {
    Session,
    Chat,
    Search,
    Info
  },
  computed: {
    sessionId () {
      let sessionId = this.$route.params.sessionId || this.$store.state.currSessionId
      return sessionId
    },
  }
}



